// hooks/useWebsiteSettings.js
import { useState, useEffect } from 'react';
import { doc, getDoc } from 'firebase/firestore';
import { db } from '@/firebase/firebase'; // Cập nhật đúng đường dẫn của bạn

function useWebsiteSettings() {
  const [settings, setSettings] = useState(null);

  useEffect(() => {
    async function fetchSettings() {
      try {
        const docRef = doc(db, 'settings', 'main'); // ← Thay bằng đúng collection/document bạn có
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
          const data = docSnap.data();
          setSettings(data);

          // Nếu cần cache
          localStorage.setItem('websiteSettings', JSON.stringify(data));
        }
      } catch (error) {
        console.error("Lỗi khi lấy metadata từ Firestore:", error);
      }
    }

    fetchSettings();
  }, []);

  return settings;
}

export default useWebsiteSettings;
