// components/MetadataUpdater.jsx
'use client';
import { useEffect } from 'react';
import useWebsiteSettings from '@/hook/useSettingWebsite';

function MetadataUpdater() {
  const settings = useWebsiteSettings();

  useEffect(() => {
    if (!settings) return;

    // Đặt tiêu đề
    if (settings.websiteName) {
      document.title = settings.websiteName;
    }

    // Meta description
    const setMeta = (name, content) => {
      let meta = document.querySelector(`meta[name="${name}"]`);
      if (!meta) {
        meta = document.createElement('meta');
        meta.setAttribute('name', name);
        document.head.appendChild(meta);
      }
      meta.setAttribute('content', content);
    };

    if (settings.description) setMeta('description', settings.description);
    if (settings.keywords) setMeta('keywords', settings.keywords);

    // Favicon
    if (settings.favicon) {
      let link = document.querySelector("link[rel~='icon']");
      if (!link) {
        link = document.createElement('link');
        link.rel = 'icon';
        document.head.appendChild(link);
      }
      link.href = settings.favicon;
    }
  }, [settings]);

  return null;
}

export default MetadataUpdater;
